.main {
    margin-bottom: 6em;
  }
  .techsection {
    width: 100%;
    text-align: center;
    height: 50vh;
    
  }
  
  .techsection > div {
    width: 11%;
    height: 16vh;
    max-height: 140px;
    box-shadow: var(--shadow);
    z-index: 1;
    border-radius: 20px;
    margin: 2.5%;
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease 0s;
    border: 2px solid #2d2c2c;
    background-color: #79d392;
  }
  .techsection > div:hover {
    background-color:#2d2c2c;
    overflow: hidden;
    transform: scale(1.08);
    box-shadow: rgba(0, 0, 0, 0.36) 0px 10px 36px 0px,
      rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
    border: 5px solid #79d392;
  }
  .techsection > div > svg {
    
    font-size: 8vh;
    /* color: var(--clr-primary); */
    color:#79d392,
  }
  .techsection > div > h5 {
    position: relative;
    top: 8px;
    color: #79d392;
    background-color: #2d2c2c;
  }
  
  @media (max-width: 600px) {
    .techsection {
      height: 120vh;
    }
    .techsection > div {
      width: 40%;
      margin-right: 5%;
      margin-bottom: 2%;
      height: 18vh;
    }
  }